.navbar::before
{
    content: "";
    position: absolute;
    bottom: 25rem;
    right: 60rem;
    width: 90rem;
    height: 35rem;
    border-radius: 17rem;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    background-color: #f8f9fc;
    background: linear-gradient(90deg,rgba(47,85,212,.3),transparent)!important;
    z-index: 0;
}